<template>
  <div>
    <div class="m-container">
      <view-header title="会员积分"></view-header>
      <div style="width: 100%" class="flex flex-row flex-start">
        <p style="margin-right: 20px">会员:</p>
        <el-input v-model="condition" style="width: 200px" placeholder="用户名/手机号"></el-input>
        <button class="btn" @click="_search">搜索</button>
      </div>
      <div class="m-table-div">
        <table>
          <thead>
            <th>会员姓名</th>
            <th>手机号</th>
            <th>积分</th>
            <th>最近增加积分时间</th>
            <th>操作</th>
          </thead>
          <tbody>
            <tr v-for="item in list" :key="item">
              <td>{{item.realName}}</td>
              <td>{{item.telephone}}</td>
              <td>{{item.bonusPoints}}</td>
              <td>{{item.completeTime | formatDate2}}</td>
              <td>
                <p class="pointer tablebtn" @click="_queryBonus(item.userId)">详情</p>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div style="width: 100%; margin: 20px 0" class="flex flex-row flex-end">
        <el-pagination
		  @size-change="_search"
          @current-change="_search"
          :page-sizes="[100, 200, 300, 400]"
          :page-size="100"
		  :current-page.sync="pageNo"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </div>
    <el-dialog
      title="积分详情"
      :visible.sync="dialogVisible1"
      width="80%">
      <p class="header">会员信息</p>
      <div class="m-table-div">
        <table>
          <thead>
            <th>会员</th>
            <th>联系电话</th>
            <th>性别</th>
            <th>出生年月</th>
            <th>注册时间</th>
          </thead>
          <tbody>
            <tr v-for="item in memberInfo" :key="item">
              <td>{{item.realName}}</td>
              <td>{{item.telphone}}</td>
              <td>{{item.sexStr}}</td>
              <td>{{item.brithday | formatDate}}({{item.age}})</td>
              <td>{{item.createTime | formatDate2}}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <p class="header">积分记录</p>
      <div class="shaixuan flex flex-row flex-start">
        <p style="margin-right: 10px">结算时间</p>
        <el-date-picker
          v-model="value1"
          type="daterange"
          align="right"
          unlink-panels
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
		  @change="pickTime"
          :picker-options="pickerOptions2">
        </el-date-picker>
      </div>
      <div class="m-table-div">
        <table>
          <thead>
            <th>结算时间</th>
            <th>交易号</th>
            <th>支付方式</th>
            <th>金额（元）</th>
            <th>增加积分</th>
            <th>截止结算日期合计</th>
          </thead>
          <tbody>
            <tr v-for="item in subList" :key="item">
              <td>{{item.completeTime | formatDate2}}</td>
              <td>{{item.subId}}</td>
              <td>手工结算</td>
              <td>{{item.settleAmount}}</td>
              <td class="red">{{item.bonusPoints}}</td>
              <td class="red">{{item.totalPoints}}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div style="width: 100%; margin: 20px 0" class="flex flex-row flex-end">
        <el-pagination
		  @size-change="_queryBonus(userId)"
          @current-change="_queryBonus(userId)"
          :page-sizes="[100, 200, 300, 400]"
          :page-size="100"
		  :current-page.sync="subPageNo"
          layout="total, sizes, prev, pager, next, jumper"
          :total="subTotal">
        </el-pagination>
      </div>
    </el-dialog>
  </div>
</template>
<script type="text/ecmascript-6">
import ViewHeader from 'base/view-header/view-header'
import {queryUserBonus,formatDate,queryBonusDetail} from '@/api'
export default {
  components: {
    ViewHeader
  },
  data () {
    return {
      condition: '',
      dialogVisible1: false,
      value1: '',
      pickerOptions2: {
        shortcuts: [{
          text: '最近一周',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近一个月',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近三个月',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
            picker.$emit('pick', [start, end])
          }
        }]
      },
	  pageSizes: [100, 200, 300, 400],
      pageSize: 100,
      total: 0,
      pageNo: 1,
      list: null,
	  userId : 0,
	  subList:null,
	  subTotal:0,
	  subPageNo:1,
	  subPageSize:100,
	  memberInfo:null
    }
  },
  filters: {
    formatDate (time) {
      var date = new Date(time)
      return formatDate(date, 'yyyy-MM')
    },
    formatDate2 (time) {
      var date = new Date(time)
      return formatDate(date, 'yyyy-MM-dd hh:mm:ss')
    }
  },
  methods: {
	_search () {
      var _this = this
      var params = new URLSearchParams()
      params.append('condition', this.condition)
	  params.append('currentPage', this.pageNo)
	  params.append('pageSize', this.pageSize)
      queryUserBonus(params).then(function (data) {
        if (!data.success) {
          alert(data.error)
        } else {
          _this.list = data.data
          _this.total = data.total
        }
      }).catch(function (data) {
        alert('系统异常，请稍后再试')
      })
    },
	_queryBonus(val) {
	  var _this4 = this
	  var params = new URLSearchParams()
	  params.append('userId', val)
	  var startTime = _this4.value1[0]
	  var endTime = _this4.value1[1]
	  if (startTime != undefined && endTime != undefined) {  
        params.append('startTime', startTime)
		params.append('endTime', endTime)
      }
	  params.append('currentPage', this.subPageNo)
	  params.append('pageSize', this.subPageSize)
	  queryBonusDetail(params).then(function (data) {
        if (!data.success) {
          alert(data.error)
        } else {
		  _this4.userId = val
          _this4.dialogVisible1 = true
		  _this4.memberInfo = data.data.memberInfo
          _this4.subList = data.data.bonusList
          _this4.subTotal = data.total
        }
      }).catch(function (data) {
        alert('系统异常，请稍后再试')
      })
	},
	pickTime() {
	   var _this2 = this
	   this._queryBonus(_this2.userId)
	}
  }
}
</script>
<style lang="stylus" scoped rel="stylesheet/stylus">
@import "~common/stylus/base"
@import "~common/stylus/variable"
.m-container
  @extend .flex
  @extend .flex-column
  width: 100%
  height: 100%
  padding: 0 10px
.green
  color: $color-back-green_0
.red
  color: $color-back-red_0
.btn
  border: 0px
  background-color: $color-back-blue_0
  color: $color-back-white_0
  font-weight: bolder
  padding: 0 15px
  margin-left: 10px
  height: 40px
  font-size: $size-16
.tablebtn
  font-size: $size-14
  color: $color-back-green_0
  font-weight: bold
.header
  margin: 20px 0
  font-size: $size-16
  color: $color-back-black_0
.shaixuan
  border: 1px solid $color-back-gray_2
  padding: 20px
</style>


